body{
  background: #282c34;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin); */
  color: white;
}


.blinking-cursor {
  color: #fff;
  font-weight: 100;
  font-size: 30px;
  -webkit-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

.background{
  position: fixed;
  z-index: -1;
  height:  100%;
  width: 100%;
}

.clouds{
  position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%,-50%);
	        transform: translate(-50%,-50%);
	/* will-change: transform; */
}

.cloud{
  position: absolute;
  border-radius: 300px;
  top: 20%;
  left: -50%;
  width: 183px;
  height: 50px;
  background: #c3c3c3;
	animation: 20s cloud linear infinite alternate-reverse;
}

.cloud:after, .cloud:before{
  content: "";
  background: #c3c3c3;
  position: absolute;
}

.cloud:after{
  height: 100px;
  width: 100px;
  top: -58px;
  left: 65px;
  border-radius: 50px;
}


.cloud:before{
  height: 80px;
  width: 80px;
  top: -39px;
  left: 14px;
  border-radius: 64px;
}

.moon{
  background: #fff;
  z-index: -2;
  position: absolute;
  width: 5em;
  height: 5em;
  border-radius: 50px;
  -webkit-transform: translate(319%, 6%);
          transform: translate(319%, 6%);
}

.star{

  height: 2px;
  width: 2px;
  background: yellow;
  border-radius: 30px;
  position: absolute;
  z-index: -2;
  
  
}


.star:after{
  content: "";
  height: 2px;
  width: 2px;
  background: yellow;
  border-radius: 30px;
  z-index: -2;
  top: 25%;
  left: 30%;
  display: inline-block;
  -webkit-animation: 5s star linear infinite;
          animation: 5s star linear infinite;
}

.star:before {
  display: inline-block;
  content: "";
  height: 2px;
  width: 2px;
  background: yellow;
  border-radius: 30px;
  z-index: -2;
  top: 25%;
  left: 30%;
  -webkit-animation: 7s star linear infinite;
          animation: 7s star linear infinite;
}

.star1-animation{
  -webkit-animation: 4s star1 linear infinite;
          animation: 4s star1 linear infinite;
  top: 20%;
  left: 50%;
}


.star2-animation{
  -webkit-animation: 8s star2 linear infinite;
          animation: 8s star2 linear infinite;
  top: 21%;
  left: 51%;
}


.star3-animation{
  -webkit-animation: 2s star1 linear infinite;
          animation: 2s star1 linear infinite;
  top: 22%;
  left: 52%;
}

@-webkit-keyframes star1{
  50%{
    color: #8e8e09
  }
  100%{
    box-shadow: 0 0 1px 1px #5f652d;
  }
}

@keyframes star1{
  50%{
    color: #8e8e09
  }
  100%{
    box-shadow: 0 0 1px 1px #5f652d;
  }
}


@-webkit-keyframes star2{
  100%{
    box-shadow: 0 0 1px 2px #5f652d;
  }
}


@keyframes star2{
  100%{
    box-shadow: 0 0 1px 2px #5f652d;
  }
}

